export default class Ticket {
  constructor(id, name, status, description, created) {
    this.id = id || null; //  (   )
    this.name = name || ""; //  
    this.status = status || false; // boolean -   
    this.description = description || ""; //  
    this.created = created || Date.now(); //   (timestamp)
  }

  clone() {
    return new Ticket(
      this.id,
      this.name,
      this.status,
      this.description,
      this.created
    );
  }
}
